export const zhTW = {
  components: {
    MuiBreadcrumbs: {
      defaultProps: {
        expandText: '',
      },
    },
    MuiTablePagination: {
      defaultProps: {
        getItemAriaLabel: (type) => {
          if (type === 'first') {
            return ''
          }

          if (type === 'last') {
            return ''
          }

          if (type === 'next') {
            return ''
          }

          return ''
        },
        labelRowsPerPage: ':',
        labelDisplayedRows: ({ from, to, count }) =>
          ` ${from}  ${to} ${
            count !== -1 ? ` ${count} ` : ` ${to} `
          }`,
      },
    },
    MuiRating: {
      defaultProps: {
        getLabelText: (value) => `${value} `,
        emptyLabelText: '',
      },
    },
    MuiAutocomplete: {
      defaultProps: {
        clearText: '',
        closeText: '',
        loadingText: '',
        noOptionsText: '',
        openText: '',
      },
    },
    MuiAlert: {
      defaultProps: {
        closeText: '',
      },
    },
  },
}
